import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { Subject } from 'rxjs';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { TableManagerService } from '../table-manager.service';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-add-table',
  templateUrl: './add-table.component.html'
})
export class AddOrUpdateTableComponent implements OnInit , OnDestroy {
  private ngUnsubcribe = new Subject();
  tableForm: FormGroup;
  logginIn = false;
  error: string;

  @Input()
  table: any;

  @Input()
  operation: 'CREATE' | 'UPDATE' = 'CREATE';
  constructor(
    // private router: Router,
     private formBuilder: FormBuilder,
     private activeModal: NgbActiveModal,
     private tableManager: TableManagerService,
    // private authManagerService: AuthManagerService,
    // private route: ActivatedRoute,

  ) {
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  // name: req.body.name,
  // status: req.body.status,
  // seating: req.body.seating,
  // owner_id: req.body.owner_id,
  // updated_date: req.body.updated_date,
  // created_date: req.body.created_date
  ngOnInit(): void {
    this.tableForm = this.formBuilder.group({
      table_id : this.table.table_id,
      name: [this.table.name, [Validators.required]],
      status: [this.table.status, Validators.required],
      seating: [this.table.seating, Validators.required],
      owner_id: [this.table.owner_id, Validators.required]
    });
  }

  submit(): void {
    if (this.operation == 'CREATE') {
      this.tableManager.addTable(this.tableForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    } else {
      this.tableManager.updateTable(this.tableForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    }
  }

  cancel(): void {
    this.activeModal.close(false);
  }
}
