import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, from } from 'rxjs';
import { TableManagerService } from '../table-manager.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { filter, takeUntil } from 'rxjs/operators';
import { AddOrUpdateTableComponent } from '../add-table/add-table.component';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { AuthUserService } from 'src/app/core/auth-user.service';

@Component({
  selector: 'app-table-list',
  templateUrl: './table-list.component.html'
})
export class TableListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  constructor(public tableManagerService: TableManagerService, private ModalService: NgbModal, private authUserService: AuthUserService) {
  }

  ngOnInit(): void {
    this.tableManagerService.getTableList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  addTableModal(): void {
    const modalRef = this.ModalService.open(AddOrUpdateTableComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    const table = {
      table_id: '',
      name: undefined,
      status: 'Active',
      seating: undefined,
      owner_id: this.authUserService.user._id
    }
    modalRef.componentInstance.table = table;
    modalRef.componentInstance.operation = 'CREATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.tableManagerService.getTableList());
  }

  deleteTable(table: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.table = table;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.tableManagerService.getTableList());
  }

  updateTable(table: any): void {
    const modalRef = this.ModalService.open(AddOrUpdateTableComponent, {
      size: 'lg',
      backdrop: 'static'
    });

    table['updated_date']= new Date().valueOf();
    
    modalRef.componentInstance.table = table;
    modalRef.componentInstance.operation = 'UPDATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.tableManagerService.getTableList());
  }
}
