import { Injectable } from '@angular/core';
import { TableService } from '../core/table.service';
import { AuthUserService } from '../core/auth-user.service';
import { Observable, BehaviorSubject } from 'rxjs';
import { TableState } from '../core/table-state';

@Injectable()
export class TableManagerService {

  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];
  private _tableList$ = new BehaviorSubject<any[]>(null);

  get tableList$(): Observable<any[]> {
    return this._tableList$.asObservable();
  }

  // private _subcategoryList$ = new BehaviorSubject<any[]>(null);
  // get subcategoryList$(): Observable<any[]> {
  //   return this._subcategoryList$.asObservable();
  // }

  constructor(private tableService: TableService, private authUserService: AuthUserService) { }



  addTable(table: any): Observable<any> {
    return this.tableService.createTable(table);
  }

  updateTable(table: any): Observable<any> {
    return this.tableService.updateTable(table);
  }

  deleteTable(id: string): Observable<any> {
    return this.tableService.deleteTable(id);
  }

  getTable(id: string): Observable<any> {
    return this.tableService.getTable(id);
  }


  getTableList(): void {
    this.tableService.getTableList(this.authUserService.user._id)
      .subscribe((data: any[]) => {
        console.log('Got Tables for you: ', data);
        this._tableList$.next(data);
        this.originalList = data;
        this._total$.next(data.length)
      });
  }

  // getSubcategoryList(): void {
  //   this.subcategoryService.getSubcategoryList(this.authUserService.user._id)
  //     .subscribe((data: any[]) => {
  //       console.log('Got Subcategories for you: ', data);
  //       this._subcategoryList$.next(data);
  //     });
  // }
  // name: req.body.name,
  // status: req.body.status,
  // cuisine: req.body.cuisine,
  // price: req.body.price,
  // subcategory: req.body.subcategory,
  // owner_id: req.body.owner_id
  searchText(): void {
    if (!this.state.searchTerm) {
      this._tableList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
        item['seating'] && item['seating'].toString().toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm)
      );
      this._tableList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}
